<template>
  <div>
    <!-- 组件展示 -->
    <com-show>
      <div class="tooltip-box">
        <y-transfer
          :data-source="mockData"
          :titles="['源列表', '目标列表']"
          :target-keys="targetKeys"
          show-search
          showSelectAll
          :operations="['haha', 'haha']"
          :filter-option="(inputValue, item) => item.title.indexOf(inputValue) !== -1"
          :show-select-all="false"
          @change="onChange"
        >
          <template
            slot="children"
            slot-scope="{ props: { direction, filteredItems, selectedKeys, disabled: listDisabled }, on: { itemSelectAll, itemSelect } }"
          >
            <y-table
              :row-selection="getRowSelection({ disabled: listDisabled, selectedKeys, itemSelectAll, itemSelect })"
              :columns="direction === 'left' ? leftColumns : rightColumns"
              :data-source="filteredItems"
              size="small"
              bordered
              :style="{ pointerEvents: listDisabled ? 'none' : null }"
              :custom-row="
                ({ key, disabled: itemDisabled }) => ({
                  on: {
                    click: () => {
                      if (itemDisabled || listDisabled) return;
                      itemSelect(key, !selectedKeys.includes(key));
                    },
                  },
                })
              "
            />
          </template>
        </y-transfer>
      </div>
    </com-show>
    <pre class="line-numbers">
        <code class="language-html" v-text="html">
        </code>
    </pre>
    <pre class="line-numbers">
        <code class="language-javascript">
          import difference from 'lodash/difference';
          const mockData = [];
          for (let i = 0; i {{ str }} 20; i++) {
            mockData.push({
              key: i.toString(),
              title: `content${i + 1}`,
              description: `description of content${i + 1}`,
              disabled: i % 4 === 0,
            });
          }

          const originTargetKeys = mockData.filter(item => +item.key % 3 > 1).map(item => item.key);

          const leftTableColumns = [
            {
              dataIndex: 'title',
              title: 'Name',
            },
            {
              dataIndex: 'description',
              title: 'Description',
            },
          ];
          const rightTableColumns = [
            {
              dataIndex: 'title',
              title: 'Name',
            },
          ];

          export default {
            data() {
              return {
                mockData,
                targetKeys: originTargetKeys,
                disabled: false,
                showSearch: false,
                leftColumns: leftTableColumns,
                rightColumns: rightTableColumns,
              };
            },
            methods: {
              onChange(nextTargetKeys) {
                this.targetKeys = nextTargetKeys;
              },

              triggerDisable(disabled) {
                this.disabled = disabled;
              },

              triggerShowSearch(showSearch) {
                this.showSearch = showSearch;
              },
              getRowSelection({ disabled, selectedKeys, itemSelectAll, itemSelect }) {
                return {
                  getCheckboxProps: item => ({ props: { disabled: disabled || item.disabled } }),
                  onSelectAll(selected, selectedRows) {
                    const treeSelectedKeys = selectedRows
                      .filter(item => !item.disabled)
                      .map(({ key }) => key);
                    const diffKeys = selected
                      ? difference(treeSelectedKeys, selectedKeys)
                      : difference(selectedKeys, treeSelectedKeys);
                    itemSelectAll(diffKeys, selected);
                  },
                  onSelect({ key }, selected) {
                    itemSelect(key, selected);
                  },
                  selectedRowKeys: selectedKeys,
                };
              },
            },
          };
        </code>
    </pre>
  </div>
</template>
<script>
import difference from 'lodash/difference';
const mockData = [];
for (let i = 0; i < 20; i++) {
  mockData.push({
    key: i.toString(),
    title: `content${i + 1}`,
    description: `description of content${i + 1}`,
    disabled: i % 4 === 0,
  });
}

const originTargetKeys = mockData.filter(item => +item.key % 3 > 1).map(item => item.key);

const leftTableColumns = [
  {
    dataIndex: 'title',
    title: 'Name',
  },
  {
    dataIndex: 'description',
    title: 'Description',
  },
];
const rightTableColumns = [
  {
    dataIndex: 'title',
    title: 'Name',
  },
];

export default {
  name: 'tabelTransfer',
  data() {
    return {
      mockData,
      targetKeys: originTargetKeys,
      leftColumns: leftTableColumns,
      rightColumns: rightTableColumns,
      str: '<',
      html: `<template>
          <div>
            <y-transfer
              :data-source="mockData"
              :target-keys="targetKeys"
              :disabled="disabled"
              :show-search="showSearch"
              :filter-option="(inputValue, item) => item.title.indexOf(inputValue) !== -1"
              :show-select-all="false"
              @change="onChange"
            >
              <template
                slot="children"
                slot-scope="{
                  props: { direction, filteredItems, selectedKeys, disabled: listDisabled },
                  on: { itemSelectAll, itemSelect },
                }"
              >
                <a-table
                  :row-selection="
                    getRowSelection({ disabled: listDisabled, selectedKeys, itemSelectAll, itemSelect })
                  "
                  :columns="direction === 'left' ? leftColumns : rightColumns"
                  :data-source="filteredItems"
                  size="small"
                  :style="{ pointerEvents: listDisabled ? 'none' : null }"
                  :custom-row="
                    ({ key, disabled: itemDisabled }) => ({
                      on: {
                        click: () => {
                          if (itemDisabled || listDisabled) return;
                          itemSelect(key, !selectedKeys.includes(key));
                        },
                      },
                    })
                  "
                />
              </template>
            </y-transfer>
            <y-switch
              un-checked-children="disabled"
              checked-children="disabled"
              :checked="disabled"
              style="margin-top: 16px"
              @change="triggerDisable"
            />
            <y-switch
              un-checked-children="showSearch"
              checked-children="showSearch"
              :checked="showSearch"
              style="margin-top: 16px"
              @change="triggerShowSearch"
            />
          </div>
        </template>`,
    };
  },
  methods: {
    onChange(nextTargetKeys) {
      this.targetKeys = nextTargetKeys;
    },
    getRowSelection({ disabled, selectedKeys, itemSelectAll, itemSelect }) {
      return {
        getCheckboxProps: item => ({ props: { disabled: disabled || item.disabled } }),
        onSelectAll(selected, selectedRows) {
          const treeSelectedKeys = selectedRows.filter(item => !item.disabled).map(({ key }) => key);
          const diffKeys = selected ? difference(treeSelectedKeys, selectedKeys) : difference(selectedKeys, treeSelectedKeys);
          itemSelectAll(diffKeys, selected);
        },
        onSelect({ key }, selected) {
          itemSelect(key, selected);
        },
        selectedRowKeys: selectedKeys,
      };
    },
  },
};
</script>
